import { Component } from '@angular/core';
import {IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';
import {Http} from "@angular/http";
import {HomePage} from "../home/home";
import {LinkManagerProvider} from "../../providers/link-manager/link-manager";

/**
 * Generated class for the TextPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-text',
  templateUrl: 'text.html',
})
export class TextPage {

  private callback:any;

  private title = '';
  private content = '';
  private isHtml = false;
  private closeAble = false

  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl:ViewController, private http:Http, private linkManager: LinkManagerProvider) {

    if(navParams.get("callback") != null) this.callback = navParams.get('callback');

    this.isHtml = navParams.get("html") == true;
    this.closeAble = navParams.get("closeable") == true;

    let link = navParams.get("target");

    if (link == undefined) {
      this.navCtrl.setRoot(TextPage,  {html: true, closable: false, target: this.linkManager.getDatenschutzAsJSON()}, {animate: true, direction: 'forward'});
    }

      this.http.get(link).subscribe(response => {
          this.content = JSON.parse(response.text())["body"][0]["value"];
          this.title = JSON.parse(response.text())["title"][0]["value"];

      })

  }

  openHomePage() {
    this.navCtrl.setRoot(HomePage);
  }

  dismiss() {
      this.viewCtrl.dismiss();
  }

  ionViewDidLoad() {
  }

  ionViewDidLeave() {
      if(this.callback != null) this.callback();
  }

}



